.app-main {
  height: 100vh;
  overflow-y: auto; /* 允许内容区域滚动 */
  padding: 20px;
  box-sizing: border-box;
}

/* 优化菜单滚动 */
.el-menu {
  height: 100%;
  border-right: none;
  overflow-y: auto;
  overflow-x: hidden;
}

/* 优化滚动条样式 */
