  padding-left: 60px !important;
}

/* 三级子菜单的缩进 */
.el-submenu .el-submenu .el-submenu .el-menu-item {
  padding-left: 80px !important;
}

/* 图标和文字的间距 */
.el-menu-item i,
.el-submenu__title i {
  margin-right: 5px;
}

/* 收起状态的样式 */
.el-menu--collapse {
  width: 60px;
  min-height: 100%;
}

/* 收起状态下的图标居中 */
.el-menu--collapse .el-menu-item i,
.el-menu--collapse .el-submenu__title i {
  margin: 0;
}
</style>



